import LRU from 'lru-cache';
var cache = null;
/**
 * using cache pools
 * @param option 
 */

export function createCachePool() {
  cache = new LRU({
    max: 30
  });
}
/**
 * get application cache by app id
 * @param id 
 */

export function getAppManifest(id) {
  return cache.get(id);
}
/**
 * set application cache by app id
 * @param id 
 */

export function setAppManifest(id, manifestInfo) {
  cache.set(id, manifestInfo);
}