import LRU from 'lru-cache';
var cache = null;
/**
 * using cache pools
 * @param option 
 */

export function createCachePool(option) {
  cache = new LRU({
    max: option.max || 10,
    dispose: function dispose(key, app) {
      app.dispose();
    }
  });
}
/**
 * get application cache by app id
 * @param id 
 */

export function getApp(id) {
  return cache.get(id);
}
/**
 * set application cache by app id
 * @param id 
 */

export function setApp(id, app) {
  cache.set(id, app);
}